@use "../variables.scss";

.header {
    position: relative;
    z-index: 1;
    display: flex;
    flex-flow: column nowrap;
    padding: 17px 20px 17px;
    // outline: 2px solid blue;

    @media (min-width: variables.$tablet-width) {
        padding: 24px 30px 0;
        flex-flow: row nowrap;
        justify-content: space-between;
        align-items: flex-end;
    }

    @media (min-width: variables.$desktop-width) {
        padding: 53px 110px 0;
    }

    &--with-border {
        border-bottom: 1px solid variables.$light-grey;

        @media (min-width: variables.$tablet-width) {
            border: none;
        }
    }
}

.header__container {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;

    @media (min-width: variables.$tablet-width) {
        border: none;
    }
}

.header__toggle {
    position: relative;
    background-color: variables.$basic-white;
    border: none;
    width: 38px;
    height: 38px;
    padding: 5px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;

    @media (min-width: variables.$tablet-width) {
        display: none;
    }

    &--no-js {
        visibility: hidden;
    }
}

.header__toggle::before {
    content: "";
    display: block;
    background-repeat: no-repeat;
    background-position: center;
}

.header__toggle--opened::before {
    background-image: url("../../img/toggle-close.svg");
    width: 20px;
    height: 20px;
}

.header__toggle--closed::before {
    background-image: url("../../img/toggle-open.svg");
    width: 24px;
    height: 18px;
}

.header__text-logo {
    @media (min-width: variables.$tablet-width) {
        display: none;
    }
}

.header__img {
    display: block;

    &--logo-phone {
        @media (min-width: variables.$tablet-width) {
            display: none;
        }
    }

    &--logo-tablet {
        display: none;

        @media (min-width: variables.$tablet-width) {
            display: block;
        }

        @media (min-width: variables.$desktop-width) {
            display: none;
        }
    }

    &--logo-desktop {
        display: none;

        @media (min-width: variables.$desktop-width) {
            display: block;
        }
    }
}

.header__navigation {
    width: 100%;
    position: absolute;
    top: 70px;
    left: 0;

    &--closed {
        display: none;
    }

    @media (min-width: variables.$tablet-width) {
        display: block;
        position: relative;
        width: 440px;
        top: 0;
        left: 0;
    }

    @media (min-width: variables.$desktop-width) {
        width: 600px;
    }
}
